.box {
  display: inline-block;
  width: 288px;
  margin: 0 57px 57px 0;
  height: 322px;
  border-radius: 5%;
}

.tags {
  font: italic 12px "Verdana";
  position: absolute;
  margin: 40px;
  text-align: right;
  width: 208px;
}
.name {
  position: absolute;
  margin: 260px 0 0 35px;
  font-size: 21px;
  font-family: Verdana;
}
.name.content {
  margin: -35px 0 0 35px;
  font-size: 18px;
  font-family: Verdana;
}

.border {
  border: 1px solid white;
  margin: 25px 0 0 25px;
  width: 238px;
  height: 272px;
  position: absolute;
  border-radius: 5%;
}

.content {
  margin: 35px 0 0 35px;
  width: 208px;
  height: 260px;
  overflow-y: scroll;
  display: table-caption;
}
.content>div {
  position: relative;
  display: table;
}

.content::-webkit-scrollbar-track {
  display: none;
}
.content::-webkit-scrollbar {
  width: 6px;
}
.content::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #666666;
}